/**
 * Mixin - Icon
 *
 * Icon stack:
 * 1. 00-config/_icons.scss (autogenerated)
 * 2. 10-base/_icons-base.scss (here)
 * 3. 30-global/_icons-application.scss (Generate default classes)
 */
/*
 * Main Icon mixin
 *
 * Can be called like @include icon($vars) { background: something; } using the
 * power of @content.
 */
/**
 * Font application to generic DOM
 */
[class*="icon--"] {
  font-family: "icons";
  display: inline-block;
  vertical-align: middle;
  line-height: 1;
  font-weight: normal;
  font-style: normal;
  speak: none;
  text-decoration: inherit;
  text-transform: none;
  text-rendering: optimizeLegibility;
}

.icon--close:before {
  content: "";
}

.icon--dflogo:before {
  content: "";
}

.icon--menu:before {
  content: "";
}

.icon--search:before {
  content: "";
}

